<template>
  <div class="wrapper">
    <div class="content" v-html="zdyztfxSzxjjajScfxbg">
    </div>
  </div>
</template>

<script>
import { get_http } from "@/api/index";
export default {
  data() {
    return {
      zdyztfxSzxjjajScfxbg: '',
      zt: ''
    };
  },
  created() {},
  mounted() {
    this.zt = this.$route.query.eachName
    this.init(this.date)
  },
  methods: {
    init(date) {
      get_http('zdyztfxSzxjjajScfxbg', {
        zt: this.zt,
        day: `[${date.startDate},${date.endDate}]`,
      })
      .then(res => {
        this.zdyztfxSzxjjajScfxbg = res.data.data;
      })
      .catch(err => {
        console.log(err);
      });
    }
  },
  computed: {
    date() {
      return this.$store.state.date;
    }
  },
  watch: {
    date(val) {
      this.init(val);
    }
  }
};
</script>

<style lang="stylus" scoped>
.wrapper
  top 0.8rem
  position absolute
  height calc(100% - 80px)
  width 100%
  .content
    position relative
    z-index 1
    width 90%
    height 100%
    display flex
    flex-direction column
    margin 0 auto
    padding .2rem .3rem
    box-sizing border-box

</style>
